export default {
  computed: {
    returnText () {
      return this.$store.state.user.returnText
    },
    toUrl () {
      return this.$store.state.user.returnUrl
    }
  },
  methods: {
    returnBack () {
      if (this.toUrl !== '') {
        this.$router.push({ path: this.toUrl })
      } else {
        this.$router.go(-1)
      }
    }
  }
}



