const course = {
  state: {
    courseName: '',
    courseId: ''
  },
  mutations: {
    SET_COURSE_NAME: (state, name) => {
      state.courseName = name
    },
    SET_COURSE_ID: (state, id) => {
      state.courseId = id
    }
  },
  actions: {
  }
}

export default course



