// import request from '../utils/request'
// import $ from 'jquery'

const user = {
  state: {
    userType: '',
    showMsg: false,
    msgType: '',
    msg: '',
    returnText: '',
    returnUrl: '',
    baseUrl: 'http://58.119.112.12:8086',
    openid: '',
    isRegAuth: false, // 是否授权过，用来控制注册时的跳转
    isReg: false // 用来控制是否要调注册
  },
  mutations: {
    SET_OPENID: (state, id) => {
      console.log('dd', id)
      state.openid = id
    },
    SHOW_TOAST: (state, msg) => {
      state.msg = msg
      state.msgType = ''
      state.showMsg = true
    },
    SHOW_ERROR_TOAST: (state, msg) => {
      state.msg = msg
      state.msgType = 'error'
      state.showMsg = true
    },
    HIDE_TOAST: (state) => {
      state.msg = ''
      state.msgType = ''
      state.showMsg = false
    },
    SET_RETURN_TEXT: (state, text) => {
      state.returnText = text
    },
    SET_RETURN_URL: (state, url) => {
      state.returnUrl = url
    },
    SET_USR_TYPE: (state, type) => {
      state.userType = type
    },
    // 是否授权过创建openid
    SET_REG_AUTH: (state, flag) => {
      state.isRegAuth = flag
    },
    // 是否注册过
    SET_REG_STATUS: (state, flag) => {
      state.isReg = flag
    }
  },
  actions: {
    getInfo ({ commit }) {
      // request({
      //   url: 'http://www.zhiheyikaoqin.cn/user/queryUserInfo?openid=' + this.state.user.openid,
      //   method: 'get'
      // }).then(res => {
      setTimeout(() => {
        const res = {
          code: 0,
          msg: '成功',
          data: {
            teaCode: 'oMXOb1UQBzYo6FfttGiXNeBYgD-Y',
            teaName: '刘小灰',
            teaSubject: 'dd',
            teaOpenid: 'oMXOb1UQBzYo6FfttGiXNeBYgD-Y',
            teaPasswd: 'dd',
            creditScore: 'dd',
            teaNickname: '小灰',
            teaHeadimgurl: 'http://thirdwx.qlogo.cn/mmopen/vi_32/DYAIOgq83eqm4oRdhlw8XkicGUWmWh8x1Mvd81arAic7e4d5TT5Hmibk0ByVuPVdXVzQYic3g27bZGTmAnVpnCwzQw/132","createTime":"2019-01-08 07:05:33","updateTime":"2019-01-08 07:05:33'
          //   stuCode: 'oMXOb1UQBzYo6FfttGiXNeBYgD-Y',
          //   stuName: '刘小灰',
          //   stuLevel: 'dd',
          //   stuGrade: 'dd',
          //   stuClass: 'dd',
          //   stuOpenid: 'oMXOb1UQBzYo6FfttGiXNeBYgD-Y',
          //   stuPasswd: 'dd',
          //   creditScore: 'dd',
          //   stuNickname: '小灰',
          //   stuHeadimgurl: 'http://thirdwx.qlogo.cn/mmopen/vi_32/DYAIOgq83eqm4oRdhlw8XkicGUWmWh8x1Mvd81arAic7e4d5TT5Hmibk0ByVuPVdXVzQYic3g27bZGTmAnVpnCwzQw/132","createTime":"2019-01-08 07:05:33","updateTime":"2019-01-08 07:05:33'
          }
        }
        if (Number(res.code) !== 0) {
        } else {
          if (this.state.user.openid === undefined) {
            commit('SET_OPENID', res.data.teaOpenid || res.data.stuOpenid)
          }
          if (res.data.stuHeadimgurl === null || res.data.stuHeadimgurl === '') {
            // 彻底没授权过
            commit('SET_REG_AUTH', false)
          } else {
            // 有过头像，获取过授权
            commit('SET_REG_AUTH', true)

            if (res.data.stuName === null || res.data.stuName === '') {
              // 没注册过
              commit('SET_REG_STATUS', false)
            } else {
              // 注册过
              commit('SET_REG_STATUS', true)
            }
            // 根据信息判断老师还是学生
            if (res.data.stuCode !== undefined) {
              commit('SET_USR_TYPE', 'student')
            } else if (res.data.teaCode !== undefined) {
              commit('SET_USR_TYPE', 'teacher')
            }
          }
        }
      }, 20)
    }
  }
}

export default user



