export default {
  name: 'app',
  watch: {
    '$route' (to, from) {
      if (from.name === null) {
        this.$store.commit('SET_OPENID', this.$route.query.id)
        this.$store.dispatch('getInfo')
      }
      console.log('this.', from, to)
    }
  }
}



